import { NavLink } from "react-router-dom";

function PriceCard(props) {
    return(
        <div className="PriceCard" onClick={props.onClick}>
            <h2 className="title">{props.plan}</h2>
            <p><span className="price">${props.price}</span>/{props.t("month")}</p>
            <p>( {props.t("paid_annually")} )</p>
            <NavLink exact to={props.linkto} activeClassName="active-link">
                <h2>{props.linkText}</h2>
                {props.p}
            </NavLink>
        </div>
    )
}

export default PriceCard;