import { useContext, useEffect, useState } from "react";
import { InitContext } from "../../contexts/initContext";
import { useTranslation } from "react-i18next";

import PriceCard from "../website/PriceCard";
import "../../styles/ServicePackage.scss"

function ServicePackage(props) {
    const { TransactionServicepackage } = useContext(InitContext);
    const { t, i18n } = useTranslation();
    const [plan, setPlan] = useState("")

    const clickPlanBtn = (e) =>{
        console.log(e)
        setPlan(e)

        fetch(TransactionServicepackage, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                'Authorization': `Bearer ${localStorage.getItem("token")}`
            },
            body: JSON.stringify({
                "action" : "Add",
                "packageid":  "package", 
                "packagetype":  "basic",
                "packagename_cht":  "基本方案",
                "packagename_en":  "Free Package",
                "description":  "Free Package 01",
                "price": "0",                
                "enabled": "1"             
            })
        })
        .then(res=>res.json())
        .then(data=> {
            console.log(data)
        })
        .catch(err => console.error(err)) 

    }

    useEffect(()=>{
        fetch(TransactionServicepackage, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                'Authorization': `Bearer ${localStorage.getItem("token")}`
            },
            body: JSON.stringify({
                "action": "Query",
                "packageid": "freepackage01",
                "packagetype": "",    
                "enabled": "",
                "updateuserid": "new"
            })
        })
        .then(res=>res.json())
        .then(data=> {
            console.log(data)
        })
        .catch(err => console.error(err))

    }, [TransactionServicepackage])

    return(
        <div className="ServicePackage">
            <div className="price-card-div">
                {/* <PriceCard t={t} plan={t("servicePackage.freepackage01")} price={0} linkto={"/register"} linkText={t("upgrade")} p={t("")}/> */}
                <PriceCard t={t} plan={t("servicePackage.package01")} price={1000} linkto={"#"} linkText={t("upgrade")} p={t("")} onClick={()=>{clickPlanBtn("basic")}}/>
                <PriceCard t={t} plan={t("servicePackage.advance")} price={2000} linkto={"#"} linkText={t("upgrade")} p={t("")} onClick={()=>{clickPlanBtn("advance")}}/>
                <PriceCard t={t} plan={t("servicePackage.vip")} price={3000} linkto={"#"} linkText={t("upgrade")} p={t("")} onClick={()=>{clickPlanBtn("vip")}}/>
                <PriceCard t={t} plan={t("servicePackage.customerize")} price={5000} linkto={"#"} linkText={t("upgrade")} p={t("")} onClick={()=>{clickPlanBtn("customerize")}}/>
            </div>
        </div>
    )
}

export default ServicePackage;